@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
:root {
  --primairy-color: rgb(238, 242, 245);
  --secondary-color: rgb(0, 81, 255);
}
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  width: 100%;
  height: 100vh;
}

.join {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: var(--primairy-color);
}

.join_container {
  width: 500px;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  background-color: white;
  border-radius: 10px;
  border: 1px solid var(--primairy-color);
  box-shadow: 5px 5px 5px 0 rgb(187, 187, 187);
}

.heading {
  width: 100%;
  height: 25%;
  background: var(--secondary-color);

  color: white;
  margin: 0;
  border-radius: 10px 10px 0 0;
  padding: 0;
  line-height: 2em;
  text-align: center;
}

.form {
  height: 100%;
  display: flex;

  justify-content: center;
  align-items: center;
}

.join_button {
  border: 0;
  background-color: var(--secondary-color);
  height: 30px;
  color: white;
  border-radius: 5px;
  margin-left: 5px;
}

.joinInput {
  border: 1px solid rgb(199, 199, 199);
  height: 28px;
  width: 200px;
  padding: 0;
}

.content {
  width: 85%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  width: 100%;
  display: flex;
  background-color: var(--primairy-color);
  height: 4em;
  color: var(--secondary-color);
  justify-content: space-between;
  border-bottom: 2px solid rgb(214, 214, 214);
  border-top-right-radius: 15px;
}
.headertext {
  margin: 0;
  padding-top: 0.5em;
  padding-left: 0.5em;
}
.headerUser {
  margin: 0;
  margin-top: 0.4em;
  margin-right: 0.4em;
}
.chatContainer {
  width: 100%;
  height: 95%;
}
.rooms {
  list-style: none;
  padding: 0;
}
.room {
  padding: 0;
  height: 3em;
  margin: 5px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  background-color: var(--primairy-color);
  cursor: pointer;
  border-bottom: 1px solid rgb(212, 212, 212);
}
.title {
  color: rgb(84, 84, 87);
  margin-left: 0.5rem;
}

.chatRoom {
  display: flex;
  width: 100%;
  height: 100%;
}

.usersList {
  padding: 0;
  width: 30%;
  background-color: var(--primairy-color);
  margin: 0;
  color: var(--secondary-color);
  list-style: none;
  font-size: 1.5em;
  border-bottom-right-radius: 15px;
}
.user {
}

.username {
  border-bottom: 1px solid rgba(167, 167, 167, 0.5);

  padding: 0.2em;
  padding-left: 0.5em;
  font-size: 0.8em;
}
.users-icon {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2.5em;
}
.chatArea {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
}
.messages {
  width: 100%;
  height: 90%;
  background-color: white;
  margin: 0;
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}

.message {
  margin: 0.5em;
  font-size: 1em;

  padding: 0em;
}
.message.others {
  float: left;
}
.message.me {
  float: right;
}
.text {
  background-color: rgb(221, 221, 221);
  padding: 0.5em;
  border-radius: 10px;
}
.text.me {
  background-color: var(--secondary-color);
  color: white;
}

.text.admin {
  text-align: center;
  background-color: rgb(211, 240, 255);
}
.sender {
  padding-left: 0.3em;
  color: rgb(94, 90, 90);
}

.inputform {
  width: 100%;
  height: 10%;
  align-items: center;
  justify-content: center;
  display: flex;
  background-color: var(--primairy-color);
}
.textInput {
  width: 100%;
  height: 50%;
  border: solid 1px rgb(209, 209, 209);
}

.sideBar {
  width: 15%;
  display: flex;
  flex-direction: column;

  color: white;
  background-color: var(--secondary-color);
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}
.myChatRoomList {
  list-style: none;
  cursor: pointer;
  padding: 0;
}
.mychat {
  padding: 1em;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgb(0, 47, 148);
}
.mychat:hover {
  background-color: var(--primairy-color);
  color: var(--secondary-color);
}
.isNew {
  border-radius: 10em;
  width: 1em;
  height: 1em;
  display: none;
}
.isNew.true {
  display: block;
}
.buttons {
  display: flex;
  width: 100%;
  height: 10%;
  cursor: pointer;
}

.btn_roomlist {
  width: 50%;
  text-align: center;
  line-height: calc(100vh * 0.1);
}
.btn_newchat {
  width: 50%;

  text-align: center;
  line-height: calc(100vh * 0.1);
}

.btn_newchat:hover,
.btn_roomlist:hover {
  background-color: var(--primairy-color);
  color: var(--secondary-color);
}

.make-form {
  height: 10%;
  width: 100%;
}
.make-form input {
  width: 80%;
  margin-left: 1em;
  margin-top: 1em;
}
.form_buttons {
  display: flex;
  width: 90%;
  margin-left: 0.7em;
}

.button {
  width: 40%;
  height: 2em;
  margin-top: 0.2em;
  margin-left: 0.4em;
  display: flex;

  align-items: center;
  justify-content: center;
  border: 1px solid #fff;
  border-radius: 10px;
  color: #fff;
}

